$sidebar-bg-color: #191919;
$submenu-bg-color: rgba(255, 255, 255, 0.05);
$submenu-bg-color-collapsed: rgba(29, 29, 29, 0.95);
$sidebar-color: #adadad;
$highlight-color: #fff;
$icon-bg-color: rgba(255, 255, 255, 0.05);
$icon-size: 35px;

$brand_border: 1px solid #252525 !important;
$brand_primary: #252525;
$brand_secondary: #D91B42;
$brand_yellow: #c49b3b;
$brand_heading: #adadad;
$brand_text: #bbbbbb;
$brand_content_bg: #f0f1f6;
$brand_content_text: #171616;

@import '~react-pro-sidebar/dist/scss/styles.scss';
@import url("https://fonts.googleapis.com/css?family=Poppins:100,200,400,700&display=swap");
@import './layout.scss';
@import './sidebar.scss';

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  height: 100vh;
  font: 16px/1.5 "Poppins", sans-serif;
  color: #fff;
}

#root {
  height: 100%;
}

.app {
  height: 100%;
  display: flex;
  position: relative;
}

.badge {
  padding: 3px 6px;
  font-size: 9px;
  letter-spacing: 1px;
  border-radius: 15px;
  &.red {
    color: #ffffff;
    background: #d63030;
  }
  &.gray {
    color: #ffffff;
    background: #777777;
  }
  &.yellow {
    color: #000000;
    background: #ffd331;
  }
}

section{
  display: flex;
  flex-direction: column;
  flex-grow: auto;
  margin: 20px;
  padding: 20px;
  border-radius: 5px;
  height: 100%;
  background-color: $brand_content_bg;
  color: $brand_content_text;
  box-shadow: 5px 5px 10px 10px #111;
  overflow-y: auto;
}

.block{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.card{
  background-color: #fff;
  margin: 10px 10px 10px 0;
  padding: 20px;
  border-radius: 5px;
  color: $brand_content_text;
  box-shadow: 0 0 10px -2px rgba(0, 0, 0, 0.075);
}

.warn{
  background-color: #e1bee7;
  font-size: 14px;
}


// table
table{
  // border-collapse: collapse;
  thead tr {
    background-color: #b0bec5;
  };
  thead th {
    padding: 5px;
    text-align: left;
    border: 2px solid #b0bec5;
    border-radius: 5px;
    color: $brand_primary;
  }
  
  tbody {
    tr td {
      padding: 5px;
    }
    tr:nth-child(even) {
      background-color: #e0e0e0;
    }
  } 
}

// modal 
.modal-content {
  position: absolute;
  top: 40%;
  left: 50%;
  right: auto;
  bottom: auto;
  margin-right: -50%;
  background-color: $brand_content_bg;
  transform: translate(-50%, -50%);
  padding: 20px;
  border-radius: 5px;
  max-width: 50%;
  color: $brand_content_text;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0, .90);
  z-index: 1010;
}

.ReactModal__Overlay {
    opacity: 0;
    transition: opacity 500ms ease-in-out;
}

.ReactModal__Overlay--after-open{
    opacity: 1;
}

.ReactModal__Overlay--before-close{
    opacity: 0;
}


// helpers

//button
button {
  padding: 5px 7px;
  cursor: pointer;
  border: thin solid $brand_primary;
  border-radius: 3px;
  background-color:$brand_primary;
  color: $brand_text;
  &:hover {
    background-color:$brand_heading;
    color:$brand_primary;
    border: thin solid $brand_heading;
  }
  &.action-text{
    color: $brand_secondary;
    &:hover{
      color: $brand_yellow;
    }
    background-color: transparent;
    border: none;
    font-size: inherit;
    font-family: inherit;
  }
}

.input-field {
  padding: 5px;
  label {
    display: block;
    padding-left: 5px;
    margin-bottom: 5px;
    font-size: 14px;
  }
}

input {
  padding: 5px;
  border: thin solid $brand_text;
  border-radius: 3px;
  width: 100%;
}

.buttons-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  margin-top: 10px;

  button {
    padding: 7px 15px;
  }
}

span {
  &.block{
    display: block;
  }
  &.inline-block {
    display: inline-block;
    margin: 0 10px;
  }
}

