.app {
  height: 100%;
  display: flex;
  position: relative;

  main {
    position: relative;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    background-color: #212121;

    .page-title{
      padding: 24px;
      background-image: url(https://res.cloudinary.com/net-ninja/image/upload/v1616238571/Net%20Ninja%20Pro/pro-images/pro-banner_rwboej.png);
    }
    
    p.page-description{
      color: $brand_heading;
    }

    footer {
      margin-top: auto;
      color: $brand_heading;
      text-align: center;
      
    }
  }
}

